.logout-btn{
    position: fixed;
    bottom: 8rem;
    right: 2rem;
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
    border: none;
}

#profile{
    position: fixed;
    bottom: 15rem;
    right: 2rem;
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
    border: none;
}

.upload-btn{
    position: fixed;
    bottom: 3rem;
    right: 2rem;
    width: 6rem;
    display: block;
}

.posts-container{
    height: 100vh;
    width: 100vw;
    background: black;
    overflow-y: scroll;
    scroll-snap-type: y mandatory;
}

.video-card{
    color: white;
    height: 100%;
    width: 30rem;
    margin: auto;
    position: relative;
    scroll-snap-align: start;
}

.like{
    font-size: 40px;
    color: rgb(204, 2, 2);
    position: absolute;
    bottom: 20vh;
    right: 10px;
}

.comment{
    font-size: 40px;
    position: absolute;
    bottom: 14vh;
    right: 10px;
}

.username{
    position: absolute;
    bottom: 20vh;
    left: 20px;
}

.song{
    position: absolute;
    left: 20px;
    bottom: 16vh;
    display: flex;
    justify-content: space-around;
}

video{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.comment-box{
    border: 1px solid;
    height: 45%;
    width: 100%;
    background: white;
    position: absolute;
    bottom: 0;
}

.comment-box-close-btn{
    position: absolute;
    top: 0;
    border: none;
    right: 0;
    color: #0984e3;
    height: 10%;
}

.all-comments{
    margin-top: 4.5vh;
    height: 80%;
    width: 100%;
    border: 1px solid black;
    color: black;
}

.comment-form{
    height: 10%;
}

.comment-form input{
    height: 100%;
    width: 80%;
    border: none;
}

.comment-form button{
    height: 100%;
    width: 20%;
    border: none;
    color: #0984e3;
}

img{
    display: block;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
}

.all-comments>div{
    display: flex;
    padding: 8px;
}

.all-comments>div>div{
    margin-left: 20px;
}

.inner-comment{
    margin-top: -1rem;
}

